html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

.section {
  background-color: #ae1332;
  margin: 0;
  padding: 0 32px;
}

body {
  color: #fff;
  background-color: #ae1332;
  font-family: DM Sans, sans-serif;
}

.list {
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

:root {
  --white-bg-text-color: #000;
  --background-color: #ae1332;
  --text-color: #fff;
  --green-fruits-card: #a4cb66;
  --yellow-fruits-card: #f5ce13;
  --red-fruits-card: #ae1332;
  --icons-color: #ae1332;
  --fw-bold: 700;
  --fw-regular: 400;
  --fw-medium: 500;
  --fs-small: 14px;
  --fs-medium: 18px;
  --fs-big: 32px;
  --fs-subtitle: 40px;
  --title-fs: 64px;
}

.contact-section {
  justify-content: center;
  margin-top: 120px;
  display: flex;
  position: relative;
  padding-bottom: 80px !important;
}

@media (min-width: 768px) {
  .contact-section {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 80px;
    margin-top: 200px;
    padding: 0;
  }

  .contact-title {
    font-size: var(--title-fs) !important;
    letter-spacing: -2.56px !important;
    max-width: 740px !important;
    line-height: 64px !important;
    top: -85px !important;
    left: 100px !important;
  }

  .flex-container {
    justify-content: center;
    align-items: center;
    gap: 24px;
    display: flex;
  }

  .register-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 80px;
    margin-top: 80px;
    display: contents !important;
  }

  .first-image {
    width: 344px;
    height: 480px;
  }

  .second-image {
    width: 344px;
    height: 480px;
    display: block !important;
  }
}

@media (min-width: 1280px) {
  .contact-title {
    left: 320px !important;
  }

  .first-image {
    width: 792px !important;
    height: 502px !important;
  }

  .second-image {
    width: 344px;
    height: 502px;
    display: block !important;
  }
}

.contact-title {
  text-align: center;
  text-shadow: 0 4px 70px rgba(0, 0, 0, .1);
  font-size: 32px;
  font-style: normal;
  font-weight: var(--fw-medium);
  text-transform: uppercase;
  max-width: 335px;
  margin: 0 auto;
  line-height: 32px;
  position: absolute;
  top: -50px;
}

.first-image {
  width: 335px;
  height: 476px;
}

.second-image {
  display: none;
}

.contact-image {
  object-fit: cover;
  object-position: center;
  border-radius: 20px;
}

.register-container {
  display: none;
}

.register-container p {
  max-width: 492px;
  text-align: center;
  text-shadow: 0 4px 70px rgba(0, 0, 0, .1);
  letter-spacing: -.56px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.content {
  width: 292px;
  height: 48px;
  border-radius: 60px;
  overflow: hidden;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, .2);
}

.subscription {
  width: 100%;
  height: 100%;
  position: relative;
}

.subscription .add-email {
  width: 100%;
  height: 100%;
  opacity: .3;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 100px;
  outline: none;
  padding: 0 15px;
}

.add-email::-ms-input-placeholder {
  letter-spacing: -.56px;
  color: #fff;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.add-email::placeholder {
  letter-spacing: -.56px;
  color: #fff;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.submit-email {
  text-align: center;
  height: 100%;
  cursor: pointer;
  background: var(--green-fruits-card);
  color: #fff;
  border: none;
  border-radius: 100px;
  margin: 1px;
  padding: 15px 35px;
  transition: width .35s ease-in-out, background .35s ease-in-out;
  position: absolute;
  top: -2px;
  right: 0;
}

.submit-email:hover, .submit-email:focus {
  background-color: #1e941e;
}

:root {
  --white-color: #fff;
  --red-color: #ae1332;
  --button-color: #a4cb66;
  --text-shadow: 0px 4px 70px rgba(0, 0, 0, .1);
}

.nav-list-item {
  letter-spacing: -.04em;
  text-align: center;
  color: var(--red-color);
  padding: 3px 0;
  font-size: 14px;
  line-height: 1.28571;
  display: block;
  position: relative;
}

.nav-list-item:hover {
  color: var(--button-color);
}

.nav-list-item:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--green-fruits-card);
  visibility: hidden;
  transition: transform .25s ease-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale(0);
}

.nav-list-item:hover:after {
  visibility: visible;
  transform-origin: 0 100%;
  transform: scaleX(1);
}

