.hero-section {
  position: relative;
  display: flex;
  flex-direction: column;
  // padding-top: 56px;
}

.hero-title-container {
  position: relative;
  max-width: 335px;
  margin: 0 auto;
  z-index: 100;
}

.hero-title {
  width: 335px;
  position: relative;
  color: #fff;
  text-align: center;
  text-shadow: 0px 4px 70px rgba(0, 0, 0, 0.1);
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 100% */
  letter-spacing: -1.12px;
  text-transform: uppercase;
  z-index: 2;
}

.image-hero-container {
  border-bottom: 1.5px solid #fff;
  border-radius: 230px;
  max-width: fit-content;
  position: relative;
  margin: 0 auto;
  top: -60px;
  z-index: 1;
}

.hero-image {
  border-radius: 500px;
  background: #fff;
  margin: auto;
  display: block;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 80%;
}

.hero-image-a {
  position: absolute;
  padding-top: 90px;
  top: 0;
  width: 66px;
  // height: 44px;
  transform: rotate(20deg);
  flex-shrink: 0;
}

.hero-image-b {
  position: absolute;
  right: 0;
  top: 0;
  width: 56px;
  // height: 56px;
  flex-shrink: 0;
}

.hero-button {
  border: none;
  color: #ae1332;
  text-align: center;
  text-shadow: 0px 4px 70px rgba(0, 0, 0, 0.1);
  font-family: 'DM Sans';
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 100% */
  letter-spacing: -1.12px;
  text-transform: uppercase;
  width: 114px;
  height: 114px;
  border-radius: 50%;
  transform: rotate(-14deg);
}

.hero-button:hover,
.hero-button:focus {
  color: #a4cb66;
  transform: rotate(-42deg);
}
.hero-link-button {
  display: flex;
  position: absolute;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.hero-cards-container {
  // display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-card-description {
  width: 278px;
  color: #fff;
  text-shadow: 0px 4px 70px rgba(0, 0, 0, 0.1);
  font-family: 'DM Sans';
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px; /* 128.571% */
  letter-spacing: -0.56px;
}

.hero-card-title {
  margin-top: 28px;
  width: 24px;
  color: #fff;
  text-align: center;
  text-shadow: 0px 4px 70px rgba(0, 0, 0, 0.1);
  font-family: 'DM Sans';
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px; /* 116.667% */
  letter-spacing: -0.48px;
}

.hero-card-text {
  margin-top: 17px;
  width: 155px;
  color: #fff;
  text-align: center;
  text-shadow: 0px 4px 70px rgba(0, 0, 0, 0.1);
  font-family: 'DM Sans';
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px; /* 116.667% */
  letter-spacing: -0.48px;
  text-transform: uppercase;
}

.hero-card-list {
  margin-top: 44px;
  display: inline-flex;
  align-items: flex-end;
  justify-content: flex-end;
  gap: 8px;
  flex-wrap: wrap;
  width: 300px;
}

.hero-card-container-left {
  margin-top: 44px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 249px;
  height: 387px;
  flex-shrink: 0;
  border-radius: 500px;
  background: #a4cb66;
}

.hero-card-container-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 249px;
  height: 387px;
  flex-shrink: 0;
  border-radius: 500px;
  background: #f5ce13;
}

.li-card {
  color: #fff;
  text-align: center;
  text-shadow: 0px 4px 70px rgba(0, 0, 0, 0.1);
  font-family: 'DM Sans';
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px; /* 116.667% */
  letter-spacing: -0.48px;
  text-transform: uppercase;
  border-radius: 1000px;
  border: 1px solid #fff;
  padding: 12px 32px;
}

.hero-container-left {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 137px;
  align-items: center;
}

.hero-container-right {
  align-items: center;
  display: flex;
  flex-direction: column-reverse;
  gap: 44px;
}

@media screen and (width >= 768px) {
  .hero-section {
    // padding-top: 64px;
  }
  .hero-title-container {
    max-width: 704px;
  }
  .hero-title {
    font-size: 50px;
    line-height: 54px;
    letter-spacing: -2px;
    width: 704px;
  }

  .hero-image {
    width: 100%;
  }

  .hero-image-a {
    width: 132px;
  }

  .hero-image-b {
    width: 108px;
  }

  .hero-cards-container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
  }
  .hero-container-right {
    flex-direction: column;
  }
}

@media screen and (width >= 1158px) {
  .hero-title-container {
    max-width: 898px;
  }
  .hero-title {
    width: 898px;
    font-size: 64px;
    line-height: 64px;
    letter-spacing: -2.56px;
  }
  .hero-cards-container {
    position: absolute;
    bottom: 0;
    width: 100%;
  }

  .hero-image-a {
    width: 184px;
  }

  .hero-image-b {
    width: 120px;
  }

  .image-hero-container {
    top: -200px;
  }
}
